/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"
import Image from '../components/image';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import { Grid, Hidden } from "@material-ui/core";
import Header from "./header"

import "../styles/layout.css"


const theme = createMuiTheme({
  palette: {
    primary: {
      // light: will be calculated from palette.primary.main,
      main: '#0d0d0d',
      // dark: will be calculated from palette.primary.main,
      // contrastText: will be calculated to contrast with palette.primary.main
    },
    secondary: {
      // light: will be calculated from palette.secondary.main,
      main: '#C11B1F',
      // dark: will be calculated from palette.secondary.main,
      // contrastText: will be calculated to contrast with palette.secondary.main
    },
    // Used by `getContrastText()` to maximize the contrast between
    // the background and the text.
    contrastThreshold: 3,
    // Used by the functions below to shift a color's luminance by approximately
    // two indexes within its tonal palette.
    // E.g., shift from Red 500 to Red 300 or Red 700.
    tonalOffset: 0.2,
  },
});

const Layout = ({ children, navNumber }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <>
      <ThemeProvider theme={theme}>
        <Header siteTitle={data.site.siteMetadata.title} navNumber={navNumber} />
        <Image
          name="bg"
          style={{
            position: "fixed",
            bottom: 0,
            right: 0,
            padding: 0,
            margin: 0,
            zIndex: 99999,
            width: typeof window !== 'undefined'?(window.innerWidth <= 960 ? 33.33 : 100):0,
            transform: 'rotateY(-180deg)',
          }}
        />
        <div
          style={{
            margin: `0 auto`,
            maxWidth: 960,
            padding: `0 1.0875rem 1.45rem`,
          }}
        >
          <Hidden implementation="css" smDown>
            <Grid container spacing={2}>
              <Grid item md={9} lg={10} xl={9}>
                <main>{children}</main>
              </Grid>
              <Grid item md={3} lg={2} xl={1}>
              </Grid>
            </Grid>
          </Hidden>
          <Hidden implementation="css" mdUp>
            <main>{children}</main>
          </Hidden>
            <main>{children}</main>
          <footer>
            © {new Date().getFullYear()}, All rights resevered -
          {` `}
            <a href="https://alireza-kavian.github.io">Alireza Kavian</a>
            <div>Font made from <a href="http://www.onlinewebfonts.com">oNline Web Fonts</a> is licensed by CC BY 3.0</div>
          </footer>
        </div>
      </ThemeProvider>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
  navNumber: PropTypes.number,
}

Layout.defaultProps = {
  navNumber: -1,
}

export default Layout
